.page-service {
  height: calc(var(--vh, 1vh) * 100);
  width: 100%;
  overflow: hidden;

  .service-container {
    margin: 0 auto;
    width: 100vw;
    height: 100vh;
    background-color: #eeeeee;
    background-size: 110vw 110vh;
    background-position: 50% 0px;
    display: flex;

    align-items: center;
    position: relative;
    background-repeat: no-repeat;
    flex-direction: column;
    justify-content: center;

    @media (max-width: 767.98px) {
      background-size: unset;
      background-position: unset;
    }
    .service-title {
      position: absolute;
      top: 10%;
      left: 15%;
      color: #fff;

      h5 {
        font-size: 14px;
        line-height: 24px;
      }
    }
    .service-menu {
      display: flex;

      gap: 50px;
      font-size: 59px;
      color: rgba(34, 34, 34, 0.2);
      z-index: 100;
      .service-brochure {
      }
      .service-proposal {
      }
      a input:hover + label {
        color: #8a8a8a;
      }
      a input:checked + label {
        color: #222;
        &:before {
          background-color: #222;
        }
      }
    }
    .service-menu-item {
      margin-top: 125px;
      display: block;
      width: 85%;
      .center {
        .slick-dots {
          bottom: -55px;
        }
        .slick-active {
          margin: 0px 0px;
        }
        .service-item {
          width: 100%;
          .service-item-box {
            width: 95%;

            position: relative;
            .item {
              width: 100%;
              height: auto;
            }
            .back {
              position: absolute;
              background-color: rgba(254, 153, 51, 0.5);
              height: 100%;
              width: 100%;
              top: 0;
              left: 0;
              opacity: 0;
              transition: 0.3s;
            }
            &:hover {
              .back {
                opacity: 1;
              }
            }
          }
        }
      }
    }

    .service-wrapper {
      text-align: left;
      width: 100%;

      gap: 120px;

      display: flex;
      justify-content: center;
      align-content: center;
      @media (max-width: 1024px) {
        left: 140px;
      }
      @media (max-width: 780px) {
        left: 50px;
      }

      .block-item {
        width: 100px;
        margin-bottom: 50px;
        h3 {
          font-size: 34px;
          color: white;
          margin-bottom: 0;
          white-space: nowrap;
          margin-bottom: 12px;
          margin-left: 5px;
          font-weight: bold;

          @media screen and (max-width: 600px) {
            font-size: 20.5px;
            margin-left: 0;
          }
        }
        h4 {
          color: white;
          margin-bottom: 0;
          white-space: nowrap;
          margin-bottom: 12px;
          margin-left: 5px;
          font-weight: bold;

          @media screen and (max-width: 600px) {
            font-size: 20.5px;
            margin-left: 0;
          }
        }
        .service-name {
          margin-top: 6px;
        }
        .color-change {
          color: orange;
        }

        a {
          color: #fff;
          text-align: left;
          p {
            font-size: 14px;
            font-family: Montserrat-ExtraLight;
            @media screen and (max-width: 600px) {
              font-size: 13.5px;
              padding: 7px 0;
            }
          }
        }

        .learn-more {
          a {
            visibility: hidden;
            color: #8e8e8e;
            padding: 15px 20px;
            white-space: nowrap;

            &:hover {
              color: white;
            }
          }
        }
        .discription {
          margin-left: 30px;
        }

        .down-menu {
          margin-left: 60px;
        }

        .email {
          transition: 0.2s;
          position: relative;
          width: 90%;
          margin-top: 6px;

          padding-right: 5%;
          height: 35px;
          line-height: 35px;
          cursor: pointer;

          &:hover {
            background-color: #fff;
            p {
              color: black;
            }

            .mail-icon {
              opacity: 1;

              p {
                visibility: visible;
              }
            }
          }

          .mail-icon {
            padding: 0 3px 3px;
            position: absolute;
            right: 10px;
            top: 10px;
            width: 15px;
            height: 15px;
            opacity: 0;
            transition: 0.3s;
            p {
              font-size: 20px;
              line-height: 15px;
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 600px) {
  .service-container {
    .service-wrapper {
      width: 90%;
    }
  }
}
